#conteiner-home-venta{
    display: flex;
    justify-content: space-between;
    box-shadow: 8px 10px 10px 2px rgb(172, 171, 166);
   

   
}

#princ{
    box-shadow: 8px 10px 10px 2px rgb(172, 171, 166);
}

#conteiner-home-venta img {
   
    box-shadow: 8px 10px 10px 2px rgb(172, 171, 166);
    opacity: 0.9;
  


  
}

.venta-text{
    text-align: center;
    width: fit-content;


}


#h2-venta{
    background-color: white;
    text-align: center;
    position: absolute;
    top: 40%;
    left: 20%;
    margin: 1%;
    background-color: rgb(158, 1, 14 );
    color: white;
    box-shadow: 8px 10px 10px 2px rgb(209, 234, 240);
    font-size: 25px;
    


   
}

#h2-venta> span{
    color: rgb(158, 1, 14 );
    background-color: black;
  }

  #conteiner-home-venta>div{
    margin: 1%;
    position: relative;
  }



 @media (max-width: 500px) {

   

  #h2-venta {

    font-size: 7px;
  }
    
  }