.gallery-container {
  background-color: #eeeeee;

  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: space-between;
  list-style: none;

}

.gallery-item {
  margin-top: 1%;
  cursor: pointer;
  width: 30%;



}

.overlay {
  background: white;
  opacity: 0.2;
  position: fixed;
  top: 0%;
  left: 0%;
  bottom: 0;
  right: 0;

}

.single-image-container {
    position: relative;
    position: fixed;
    top:  0%;
    left: 0%;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;

  



  
}

.single-image-container * {
  pointer-events: all;

}



.item-venta{
    box-shadow: 8px 10px 10px 2px rgb(16, 2, 2);
}

.units{
    display: flex;
    justify-content: space-between;
    align-self: flex-start;


}

.unit-1{
justify-self: flex-end;
 
}



.item{
    background-color: rgb(230, 230, 229);

}


@media (max-width: 500px) {
  .gallery-item {
    margin: 2%;
    cursor: pointer;
    width: 45%;
  
  
  
  }
}