    display: flex;
    justify-content: space-around;
    margin: 2%;

 
}


.units2{
width: 50%;
display: flex;


}
.units2 img{

    width: 50%;

}


.main-blog-home{
    overflow:hidden;
     width: 50%;
    box-shadow: 8px 10px 10px 2px rgb(31, 31, 31);
}

.main-blog-home>img{
    width: 100%;
    height: 100%;
    box-shadow: 8px 10px 10px 2px rgb(155, 153, 153);
}



.homeNews{
   overflow: scroll;
    height: 400px;
    box-shadow: 8px 10px 10px 2px rgb(155, 153, 153);
    margin-left: 2%;

}

.news{
    display: flex;
    flex-wrap: wrap;
    box-shadow: 8px 10px 10px 2px rgb(22, 21, 21);
   


}

.imgenHomeNews>img{
    box-shadow: 8px 10px 10px 2px rgb(22, 21, 21);
    height: 100%;
    width: 90%;
}
.newsTitle{
    width: 100%;
    text-align: center;
}

.imgenHomeNews {
   display: flex;
   align-items: center;
   justify-content: center;
    width: 50%;  
    height: 150px;
    
  
    }

    .conteinerTexnNews{
        width: 70%;
        height: 100%;
    }

    .homeContent {
        display: flex;
        align-items: center;
    }


    @media (max-width: 500px) {

     #blog-home-conteiner{
        flex-direction: column;
     }
    .main-blog-home{
        width: 100%;
