
.galeria-home-conteiner{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.galeria-home-content{
    display: flex;
    justify-content: space-around;
    position: relative;
    width: 30%;
    margin-top: 1%;
    box-shadow: 8px 10px 10px 2px rgb(172, 171, 166);
    height: 500px;
    overflow: hidden;

}

.galeria-home-content>img{
/* height: 100%; */

width: 100%;
box-shadow: 8px 10px 10px 2px rgb(172, 171, 166);



   
}


.h2-galeria-home {
    text-align: center;
    position: absolute;
    left: 3%;
    bottom: 5%;
    color: white;
    background-color: black;
    box-shadow: 8px 10px 10px 2px white;
    overflow: hidden;


   
}
#textBlog{
   text-align: center;

}

@media (max-width: 500px) {

    .galeria-home-conteiner{
        flex-direction: column;
    }

    .galeria-home-content{
        width: 90%;
        height: 400px;
    }

      
    }