.swapConteiner{

    width: 100%;
    height: 850px; 
    cursor: pointer;
    display: flex;
    justify-content: center;
    
 


}

.swapContent{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
 
}

.swapContent>img{
 
    display: block;
    width: 98%;
    height: 98%;
    object-fit: cover;
    box-shadow: 8px 10px 10px 2px rgb(89, 88, 88);
}

@media (max-width: 500px){

    .swapConteiner{
        height: 400px;
    }

}