body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.active{
  cursor: pointer; 
  color: black;
  background-color: white;


}

a{
  color: white;
  border: none;


}



#root{
height: 100vh;
}




.splash-regular {
  font-family: "Splash", cursive;
  font-weight: 400;
  font-style: normal;
}

header{
  position: relative;
}

header .cart-bottom  {
  cursor: pointer;
  float: right;
  transition: color, transform 500ms ease;
}

header .cart-bottom :hover, 
header .cart-bottom .active {  
  transform: scale(1.5);
  color: white;
  background-color: black;  


  
} 

header .shop-cart {

  position: absolute;
  height: 200px;
  top: 90px;
  right: 0;
  width: 450px;
  background: black;
  box-shadow: 0px 2px 10px 2px rgb(255, 255, 255);
  z-index: 1000;

}


header .login-bottom  {
  cursor: pointer;
  float: right;
  transition: color, transform 500ms ease;
}

header .login-bottom :hover, 
header .login-bottom .active {

  
  transform: scale(1.5);
  color: white;
  background-color: black;  


  
} 

header .login-icon {

  position: absolute;
  height: 200px;
  top: 90px;
  right: 0;
  width: 450px;
  background: black;
  box-shadow: 0px 2px 10px 2px rgb(255, 255, 255);
  z-index: 1000;

}


@media (min-width: 350px) {

  .logo{
    display: none
    ;
  }
}