header{
    display: flex;
    background-color: black;
    height: 10%;
    align-items: center;
    justify-content: center;

    color: white;
    gap:25px
 

    


}

.linkMenu{
    display: flex;
    align-items: center;
   justify-content: center;
    font-size: 20px;

    width: 10%;   
 
}
.linkMenu:hover{
    background-color: white;
    color: black;
}

footer{
    display: flex;
    background-color: black;
    width: 100%;
    justify-content: space-between;
  
    
}

#outlet{

    min-height: 90%;



}


form{
    display: flex;
    flex-wrap: wrap;
    background-color: transparent;
    color: white;
    gap: 10px;
    text-align: center;
    justify-content: center;
    padding: 1%;
    box-shadow: 0px 2px 10px 2px rgb(255, 255, 255);
    z-index: 1000;
    height: 100%;

    
}

form > h2 {
    width: 100%;

}

input {
    background-color: transparent;
    color: white;
    height: 40px;
    width: 80%;
    border: 1px solid white; 
    cursor: pointer;

    




}

textarea{
    background-color: transparent;
    color: white;
    border: 1px solid white; 
    width: 80%;

    box-shadow: 0px 2px 10px 2px rgb(255, 255, 255);

    
}

.form-footer-conteiner{
    margin: 1%;
    justify-self: flex-end;
    background-color: transparent;
    width: 30%;
    color: white;
    position: relative;
    
}

.units{
width: 30%;
align-self: flex-end;
justify-self: center;
display: flex;
justify-content: center;
}

#lastUnit{
    position: absolute;
    right: 0%;
   
}

#firstUnit{

    position: absolute;
    left: 0%;

}



#footer-info{
    width: 40%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: fit-content;


}

.logo{
 
    
height: 90%;
width: 10%;

display: flex;
justify-content: center;
box-shadow: 0px 2px 10px 2px rgb(255, 255, 255);



    
}
.logo img{
    height: 100%;
    width: 100%;
   
}



.enlsces{
    display: flex;
    justify-content: center;
    gap: 15px;
    width: 70%;
    height: 70%;
    box-shadow: 0px 1px 10px 2px rgb(255, 255, 255);
   

    

    
  }

  .linkMenu{
    overflow: hidden;


  }

.loginConteiner{
    width: 10%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
   height: 90%;
    box-shadow: 0px 2px 10px 2px rgb(255, 255, 255);
}
  

.futuroCercano{
    color: white;
    text-align: center;
}


input::-webkit-input-placeholder { color: white; font-size: 15px; font-weight: bold; }
textarea::-webkit-input-placeholder { color: white; font-size: 15px; font-weight: bold; }

.logo-footer{
    height: 80px;
    width: 20%;
    
}


.logo-footer > img {
    height: 100%;
    width: 100%;
}

.textos-footer{
    color: white;

}

.textos-footer >p {
    width: 100%; 
    text-align: center;
 


}

.icons-footer{
    gap: 5px;
    display: flex;
    color: rgb(222, 137, 183);
    cursor: pointer;

}



.item-icon-footer{
    height: 30px;
}

.botone-footer{
    display: flex;
    justify-content: space-between;
    gap: 10px;

}


.botone-footer > button {
    background-color: transparent;
    color: white;

    
}

.ubicacion{
    color: white;
    width: fit-content;

}

.ubicacion > p {
    width: 100%;
}
.horarios{
    color: white;
    width: fit-content;
    
}
.horarios > p {
    width: 100%;
}


.infoConteiner{
    display: flex;
    justify-content: space-around;
    width: 100%;
}


@media (max-width: 500px) {

    .logo {
     height: 50%;
    }

    .enlsces>a{
        font-size: 7px;
        width: fit-content;
    }
    .loginConteiner{
        display: none;
    }

    footer {
    flex-wrap: wrap;
        
    }

    #footer-info{
        order: 2;
        width: 100%;
        
       
    }

    .form-footer-conteiner{
        width: 100%;
        order: 1;
    }

   
    

    
    #outlet{
        min-height: 40%;
    }

   
    


  }

  @media (min-width: 1300px){

    .units{
        width: 20%;
    }

    #lastUnit{
        position: absolute;
        right: 5%;
       
    }
    
    #firstUnit{
    
        position: absolute;
        left: 5%;
    
    }





  }

