#artistas-conteiner{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.artista{
    width: 30%;
    margin-top: 1%;
    box-shadow: 8px 10px 10px 2px rgb(16, 2, 2);
    margin-bottom: 1%;
}

.foto{
    padding: 5%;
  
}

.foto>img{
    border-radius: 350px;
    box-shadow: 8px 10px 10px 2px rgb(16, 2, 2);

}

.textoArt{
    cursor: pointer;
   

}
@media (max-width: 500px){
    .artista{
        width: 100%;
    }

}

