#expoConteiner{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: rgb(238, 238, 238);
}


.item-card {

    display: flex;
    position: relative;
    border-radius: 10px;
    cursor: pointer;
    overflow: hidden;
    transition: transform 0.5s;
    border-radius: 10px;
    margin: 1%;
    width: 30%;
   

  }
  
  .card-back {
    color: #333;
    position: static;
    background-color: #fff;
    
 


    
  }
  
  .item-card img {
    max-width: 100%;
    max-height: 100%;
  }

  @media (max-width: 500px) {


    .item-card {
      width: 100%;
    }

  }