.yuji-boku-regular {
    font-family: "Yuji Boku", serif;
    font-weight: 400;
    font-style: normal;
  }
  

  .rubik-glitch-regular {
    font-family: "Rubik Glitch", system-ui;
    font-weight: 400;
    font-style: normal;
  }
  



#carusel{
    width: 32%;
    margin: 1%;

  
}

#carusel img {
    width: 100%;
  

}

#main-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.swiper-button-prev{
    color: black;
    display: none;
}

.swiper-button-next{
    color: black;
    display: none;

}

.swiper-pagination-bullet{
    display: none;
}

#some-info{
    /* background-color: rgb(247, 247, 247); */
   
    width: 95%;
    margin: 3%;
}



#test{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#test>img{

    width: 20%;
    height: 200px;
    margin: 1%;
}

#exp-now-conteiter{
   display: flex;
   justify-content: space-between;
   width: 70%;
}

.exposision-now{
    display: flex;
    justify-content: center;
    width: fit-content;
    height: fit-content;
}
.exposision-now img{
    box-shadow: 8px 10px 10px 2px rgb(16, 2, 2);
    border-radius: 1px;


    
}

.artistas{
    display: flex;
    flex-wrap: wrap; 
    text-align: center;
    justify-content: center;
  


}

.artistas>h2{
    width: 100%;
}

.arista-conteiner{
    margin: 1%;
    width: 22%;

  
}

.arista-conteiner>img{

    box-shadow: 8px 10px 10px 2px rgb(16, 2, 2);
}


#h2-expasion {
    background-color: white;
    text-align: center;
    position: absolute;
    left: 3%;
    bottom: 5%;
    color: rgb(158, 1, 14);
    background-color: black;
    box-shadow: 8px 10px 10px 2px rgb(209, 234, 240);


   
}

.atual-section-content{
    position: relative;
    display: flex;
    
}


.mrs-sheppards-regular {
    font-family: "Mrs Sheppards", cursive;
    font-weight: 400;
    font-style: normal;
  }
  

  #h2-expasion> span{
    color: white;
    background-color: rgb(158, 1, 14 );
  }

 #section-conteiner{
    display: flex;
    justify-content: space-evenly
 }

 .units-section{
  display: flex;
  flex-wrap: wrap;
    padding: 2%; 
    width: 15%;



  
 }

 .units-section>img{
  
  width: 100%;
 }

.units-section :nth-child(2){
margin-top: 10%;
}

 .atual-section-content>image{
    width: 100%;
 }

 .cuadro{
    box-shadow: 8px 10px 10px 2px rgb(16, 2, 2);
   
 }


 @media (max-width: 500px) {

    #h2-expasion{
     
        font-size: 10px;
    }

    .units-section{
        display: none;
    }

    #exp-now-conteiter{
        width: 90%;
    }



#carusel{
    width: 80%;
    margin: 1%;

  
}

  
    
  }